.search-form {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 10px;
}

input {
    width: 800px; 
    border: none;
    outline: none;
    background-color: #f2f2f2;
    padding: 10px 20px;
    border-radius: 20px;
    font-size: 16px;
    transition: 0.3s ease;
}


.gallery {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    list-style: none;
    margin: 0 auto;
    padding: 0;
}

.image {
    width: 300px;
}

.stats-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 14px;
    gap: 5px;
}

.stats {
    display: flex;
    justify-content: space-around;
    padding: 10px;
}

.photo-card {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    overflow: hidden;
    background: white;
}